'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { apiClient } from '@/lib/auth-context';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { CheckCircle, Copy } from 'lucide-react';

// Schema for initial customer creation (Banker side)
const createCustomerSchema = z.object({
    firstName: z.string().min(1, 'First name is required'),
    lastName: z.string().min(1, 'Last name is required'),
    email: z.string().email('Invalid email address'),
    dateOfBirth: z.string().min(1, 'Date of birth is required'),
});

type CreateForm = z.infer<typeof createCustomerSchema>;

interface CustomerCredentials {
    customerNumber: string;
    email: string;
    tempPassword: string;
}

export default function NewCustomerPage() {
    const router = useRouter();
    const [error, setError] = useState<string | null>(null);
    const [submitting, setSubmitting] = useState(false);
    const [credentials, setCredentials] = useState<CustomerCredentials | null>(null);
    const [copied, setCopied] = useState(false);

    const { register, handleSubmit, formState: { errors } } = useForm<CreateForm>({
        resolver: zodResolver(createCustomerSchema),
    });

    const onSubmit = async (data: CreateForm) => {
        setSubmitting(true);
        setError(null);
        try {
            const result = await apiClient<{
                customerId: number;
                customerNumber: string;
                email: string;
                tempPassword: string;
            }>('/banker/customers/create', {
                method: 'POST',
                body: JSON.stringify(data),
            });

            if (!result.success || !result.data) {
                throw new Error(result.error || 'Failed to create customer');
            }

            // Show credentials dialog instead of redirecting
            setCredentials({
                customerNumber: result.data.customerNumber,
                email: result.data.email,
                tempPassword: result.data.tempPassword,
            });
        } catch (err: any) {
            setError(err.message);
        } finally {
            setSubmitting(false);
        }
    };

    const copyCredentials = () => {
        if (credentials) {
            const text = `Customer Login Credentials:\nEmail: ${credentials.email}\nPassword: ${credentials.tempPassword}`;
            navigator.clipboard.writeText(text);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        }
    };

    return (
        <div className="max-w-2xl mx-auto py-8">
            <div className="mb-6">
                <h1 className="text-2xl font-bold text-slate-900">New Customer Onboarding</h1>
                <p className="text-slate-600">Create a customer record to begin the onboarding process.</p>
            </div>

            <Card>
                <CardHeader>
                    <CardTitle>Customer Details</CardTitle>
                    <CardDescription>Enter the customer's basic information.</CardDescription>
                </CardHeader>
                <CardContent>
                    <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                        {error && (
                            <Alert variant="destructive">
                                <AlertDescription>{error}</AlertDescription>
                            </Alert>
                        )}

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                            <div className="space-y-2">
                                <Label htmlFor="firstName">First Name</Label>
                                <Input id="firstName" {...register('firstName')} />
                                {errors.firstName && <span className="text-sm text-red-500">{errors.firstName.message}</span>}
                            </div>
                            <div className="space-y-2">
                                <Label htmlFor="lastName">Last Name</Label>
                                <Input id="lastName" {...register('lastName')} />
                                {errors.lastName && <span className="text-sm text-red-500">{errors.lastName.message}</span>}
                            </div>
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="email">Email Address</Label>
                            <Input id="email" type="email" {...register('email')} />
                            {errors.email && <span className="text-sm text-red-500">{errors.email.message}</span>}
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="dateOfBirth">Date of Birth</Label>
                            <Input id="dateOfBirth" type="date" {...register('dateOfBirth')} />
                            {errors.dateOfBirth && <span className="text-sm text-red-500">{errors.dateOfBirth.message}</span>}
                        </div>

                        <div className="pt-4 flex justify-end gap-2">
                            <Button variant="outline" type="button" onClick={() => router.back()}>Cancel</Button>
                            <Button type="submit" disabled={submitting}>
                                {submitting ? 'Creating...' : 'Create Customer'}
                            </Button>
                        </div>
                    </form>
                </CardContent>
            </Card>

            {/* Success Dialog with Credentials */}
            <Dialog open={!!credentials} onOpenChange={() => { }}>
                <DialogContent>
                    <DialogHeader>
                        <DialogTitle className="flex items-center gap-2">
                            <CheckCircle className="h-5 w-5 text-green-600" />
                            Customer Created Successfully
                        </DialogTitle>
                        <DialogDescription>
                            Share these credentials with the customer securely. They should change their password on first login.
                        </DialogDescription>
                    </DialogHeader>

                    {credentials && (
                        <div className="py-4 space-y-4">
                            <div className="bg-slate-50 p-4 rounded-lg space-y-3">
                                <div>
                                    <Label className="text-xs text-slate-500">Customer Number</Label>
                                    <p className="font-mono font-medium">{credentials.customerNumber}</p>
                                </div>
                                <div>
                                    <Label className="text-xs text-slate-500">Email (Login)</Label>
                                    <p className="font-mono font-medium">{credentials.email}</p>
                                </div>
                                <div>
                                    <Label className="text-xs text-slate-500">Temporary Password</Label>
                                    <p className="font-mono font-bold text-lg text-blue-600">{credentials.tempPassword}</p>
                                </div>
                            </div>

                            <Button
                                variant="outline"
                                className="w-full"
                                onClick={copyCredentials}
                            >
                                <Copy className="h-4 w-4 mr-2" />
                                {copied ? 'Copied!' : 'Copy Credentials'}
                            </Button>
                        </div>
                    )}

                    <DialogFooter>
                        <Button onClick={() => router.push('/banker/customers')}>
                            Go to Customer List
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
        </div>
    );
}
